import axios from "axios";
import authHeader from "./authHeader";
// import Cookies from "js-cookie";
import AuthService from "./authService";
// console.log(userId);

class userFastingService {
  async getWeeklyFastingData() {
    const userId = AuthService.getCurrentUser();
    const API_URL = `/api/user/${userId}/`;
    try {
      let response = await axios(API_URL + "getweeklyfastingData", {
        headers: authHeader(),
      });
      // let resData = await response.json();
      // console.log(resData);
      // console.log("s wweekly:" + response.data);
      return response.data;
    } catch (error) {
      return error;
    }
  }

  async getFastingDetails() {
    const userId = AuthService.getCurrentUser();
    const API_URL = `/api/user/${userId}/`;
    try {
      let response = await axios(API_URL + "getfastingdetails", {
        headers: authHeader(),
      });
      // console.log("s statresponse:" + response.data);
      return response.data;
    } catch (error) {
      return error;
    }
  }

  async postUserFastingDetails(values) {
    const userId = AuthService.getCurrentUser();
    const API_URL = `/api/user/${userId}/`;
    let options = {
      url: API_URL + "fastingdetails",
      method: "POST",
      headers: authHeader(),
      data: values,
    };
    try {
      let response = await axios(options);
      return response.data;
    } catch (error) {
      return error;
    }
  }

  async postUserFastingHistory(values) {
    const userId = AuthService.getCurrentUser();
    const API_URL = `/api/user/${userId}/`;
    let options = {
      url: API_URL + "fastinghistory",
      method: "POST",
      headers: authHeader(),
      data: values,
    };
    try {
      let response = await axios(options);
      return response.data;
    } catch (error) {
      return error;
    }
  }
}

export default new userFastingService();
