import React, { Component, useEffect, useState } from "react";
import "./index.css";
import { withRouter } from "react-router-dom";
import WeeklyFastingGraph from "../WeeklyFastingGraph";
import Timer from "../Timer";
import StatContainer from "../StatContainer";
import Cookies from "js-cookie";
import UserFastingService from "../../services/userFastingService";

class Dashboard extends Component {
  constructor(props) {
    super(props);
    const { history } = this.props;
    const jwtToken = Cookies.get("jwt_token");
    if (jwtToken === undefined) {
      history.push("/login");
    }
    this.state = {
      weeklyFastingData: "",
      fastingStatData: "",
      trigger: false,
    };
    this.handleCallback = this.handleCallback.bind(this);
  }

  handleCallback(childData) {
    this.setState({ trigger: childData });
  }

  async getWeeklyFastingData() {
    try {
      let response = await UserFastingService.getWeeklyFastingData();
      // console.log("server fasting response" + JSON.stringify(response.data));
      this.setState({ weeklyFastingData: response.data });
      this.setState((prevState) => {
        this.state.trigger = !prevState.trigger;
      });
    } catch (error) {}
  }
  async getFastingStatData() {
    try {
      let response = await UserFastingService.getFastingDetails();
      // console.log("server fasting response" + response.data);
      this.setState({ fastingStatData: response.data });
    } catch (error) {}
  }
  componentDidMount() {
    this.getWeeklyFastingData();
    this.getFastingStatData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState.trigger !== this.state.trigger) {
      this.getWeeklyFastingData();
      this.getFastingStatData();
    }
  }

  render() {
    const { weeklyFastingData, fastingStatData, trigger } = this.state;
    // console.log("state weekly" + weeklyFastingData);
    // console.log("state stat" + fastingStatData);

    return (
      <div className="container bg-container mt-5">
        <div className="row">
          <div className="col-12 col-md-5 mb-5">
            <Timer fromParent={this.handleCallback} trigger={trigger} />
          </div>
          <div className="col-12 col-md-7 mb-5">
            {weeklyFastingData && (
              <WeeklyFastingGraph weeklyFastingData={weeklyFastingData} />
            )}
          </div>
          <div className="col-12">
            {fastingStatData && <StatContainer statData={fastingStatData} />}
          </div>
        </div>
      </div>
    );
  }
}

export default withRouter(Dashboard);
