import React, { Component } from "react";
import "./index.css";
import { Bar } from "react-chartjs-2";
import { format, fromUnixTime } from "date-fns";

// const randomInt = () => Math.floor(Math.random() * (10 - 1 + 1)) + 1;

class WeeklyFastingGraph extends Component {
  constructor(props) {
    super(props);
  }

  render() {
    let label_list = [];
    let values_list = [];
    let colors_list = [];
    this.props.weeklyFastingData.forEach((element) => {
      let datetimestamp = element.started_at;
      let dateLabel = format(fromUnixTime(datetimestamp), "MMM do");
      label_list.push(dateLabel);
      values_list.push(Math.round(element.fasting_time / 3600));
      if (element.fasting_time >= 16 * 3600) {
        colors_list.push("#5DD362");
      } else if (
        (element.fasting_time >= 8 * 3600) &
        (element.fasting_time < 16 * 3600)
      ) {
        colors_list.push("#A3A3A3");
      } else {
        colors_list.push("#EDB98A");
      }
    });
    let barData = {
      labels: label_list,
      datasets: [
        {
          label: "Recent Fasts",
          barPercentage: 0.8,
          barThickness: 10,
          // maxBarThickness: 6,
          // minBarLength: 2,
          data: values_list,
          backgroundColor: colors_list,
          borderWidth: 0,
          borderRadius: 10,
          pointStyle: "circle",
          drawtick: false,
        },
      ],
    };
    // set options
    let barOptions = {
      options: {
        scales: {
          xAxes: [
            {
              barPercentage: 0.4,
            },
            {
              linewidth: 0,
            },
          ],
          y: {
            max: 24,
            min: 0,
            ticks: {
              stepSize: 2,
            },

            linewidth: 0.1,
          },
        },
        title: {
          display: true,
          text: "Data Orgranized In Bars",
          fontSize: 25,
        },
        legend: {
          display: true,
          position: "top",
        },
      },
    };
    return (
      <div className="bg-weekly-graph d-flex flex-column justify-content-center">
        <div className="d-flex flex-row justify-content-between align-items-center">
          <h5 className="head-text">
            Past 7 Avg:
            {Math.floor(values_list.reduce((a, b) => a + b, 0) / 7)} h
          </h5>
        </div>
        <Bar data={barData} options={barOptions.options} />
      </div>
    );
  }
}

export default WeeklyFastingGraph;
