import React, { useState, useEffect } from "react";
import { Link, withRouter } from "react-router-dom";
import "./index.css";
import { validate } from "./validate";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import AuthService from "../../services/authService";

const Register = (props) => {
  const [values, setValues] = useState({});
  const [errors, setErrors] = useState({});
  const [isSubmitting, setIsSubmitting] = useState(false);

  useEffect(() => {
    // setErrors(validate(values));
    if (Object.keys(errors).length === 0 && isSubmitting) {
      registerUser(values);
    }
  }, [errors]);

  let registerUser = async (values) => {
    let response = await AuthService.register(values);
    setIsSubmitting(false);

    if (response.status === 200) {
      const { history } = props;
      toast.success("user registered succesfully");
      history.replace("/login");
    }
  };

  const handleSubmit = async (event) => {
    if (event) event.preventDefault();
    setErrors(validate(values));
    setIsSubmitting(true);
  };

  const handleChange = (event) => {
    event.persist();
    setValues((values) => ({
      ...values,
      [event.target.name]: event.target.value,
    }));
  };

  return (
    <>
      <div className="bg-container">
        <div className="container">
          <div className="row mt-5 justify-content-center">
            <div className="col-12 col-md-7  bg-card ">
              <div className="pt-3 pb-3">
                <h2 className="card-title">Sign Up for free</h2>

                <form onSubmit={handleSubmit}>
                  <div className="form-group row justify-content-between ">
                    <div className="col-4 mb-0">
                      <label className="label-text" htmlFor="firstName">
                        First Name
                      </label>
                      <input
                        autoComplete="off"
                        className={`form-control input-sm ${
                          errors.firstName && "text-danger"
                        }`}
                        placeholder="First Name"
                        type="text"
                        name="firstName"
                        value={values.firstName}
                        onChange={handleChange}
                      />
                      {errors.firstName && (
                        <p className={`form-control-error text-danger`}>
                          {errors.firstName}
                        </p>
                      )}
                    </div>
                    <div className="col-4">
                      <label className="label-text" htmlFor="lastName">
                        Last Name
                      </label>
                      <input
                        className={`form-control input-sm ${
                          errors.lastName && "text-danger"
                        }`}
                        placeholder="Last Name"
                        type="text"
                        name="lastName"
                        value={values.lastName}
                        onChange={handleChange}
                      />
                      {errors.lastName && (
                        <p className="form-control-error">{errors.lastName}</p>
                      )}
                    </div>
                  </div>

                  <div className="form-group ">
                    <label className="label-text" htmlFor="email">
                      Email
                    </label>
                    <input
                      type="email"
                      className={`form-control input-sm ${
                        errors.email && "text-danger"
                      }`}
                      id="email"
                      placeholder="Email"
                      name="email"
                      value={values.email}
                      onChange={handleChange}
                    />
                    {errors.email && (
                      <p className="form-control-error text-danger">
                        {errors.email}
                      </p>
                    )}
                  </div>
                  <div className="form-group ">
                    <label className="label-text" htmlFor="password">
                      Password
                    </label>
                    <input
                      type="password"
                      className={`form-control input-sm ${
                        errors.password && "text-danger"
                      }`}
                      id="password"
                      name="password"
                      value={values.password}
                      placeholder="Password"
                      onChange={handleChange}
                    />
                    {errors.password && (
                      <p className="form-control-error text-danger">
                        {errors.password}
                      </p>
                    )}
                  </div>
                  <div className="form-group ">
                    <label className="label-text" htmlFor="confirmpassword">
                      Confirm Password
                    </label>
                    <input
                      type="password"
                      className={`form-control input-sm ${
                        errors.confirmpassword && "text-danger"
                      }`}
                      id="confirm-password"
                      name="confirmpassword"
                      value={values.confirmpassword}
                      placeholder="confirm-password"
                      onChange={handleChange}
                    />
                    {errors.confirmpassword && (
                      <p className="form-control-error text-danger">
                        {errors.confirmpassword}
                      </p>
                    )}
                  </div>
                  <div className="mb-1">
                    <button
                      type="submit"
                      className="btn btn-primary align-self-stretch"
                    >
                      Create Account
                    </button>
                  </div>

                  <div className="d-flex flex-row align-items-start justify-content-start mt-2">
                    <p>Already Have an account ?</p>
                    <Link to="/" className="ml-2 text-primary">
                      Login
                    </Link>
                  </div>
                </form>
              </div>
            </div>

            <div className="col-12  col-md-5 pt-4 d-flex flex-column justify-content-center cta-part">
              <h1 className="text-center heading">Loose Weight for Good</h1>
            </div>
          </div>
        </div>
      </div>
      <ToastContainer />
    </>
  );
};

export default withRouter(Register);
